import React from 'react';
import { Header } from '../Home/Header';
import {PrivacyContents} from './privacycontent/privacycontent';
import {Privacytext} from './privacytext/privacytext';
import {Aboutfooter} from '../About/Aboutfooter/aboutfooter';

export class Privacy extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    render() {
        return (
            <>
                <div>
                    <Header />
                </div>
                <div>
                    <PrivacyContents />
                </div>
                <div>
                    <Privacytext/>
                </div>
                <br/>
                <div>
                    <Aboutfooter />
                </div>
            </>
        )
    }
}