import React from 'react';
import { Link } from 'react-router-dom';
import './homecontent.css';

export function Contents() {
    return (
        <>
            <div class="card img-fluid" style={{ height: '60vh' }} >
                <div className='contentback' >
                    {/* <div><img src='/pics/reddd.png' style={{
                        width: '200px', height: '520px',
                        position: 'absolute', left: '20px',
                        top: '20px'

                    }}
                    />
                    </div> */}
                </div>

                <div class="card-img-overlay" style={{ backgroundColor: 'black', color: 'white', width: '54%', margin: 'auto', height: '43%' }}>
                    <div className=' ovrslayes'>
                        <h1 style={{ fontWeight: 'bold' }}>
                            STUDENT OF LIFE
                       </h1>
                        <div className='homedis'>
                            <p>Make a move in the world of Digital Education. If you already have an account, sign in to use Being Pupil on the web.</p>
                        </div>

                        <div style={{ display: 'flex', justifyContent: 'space-around', width: '50%', margin: 'auto', marginTop: '30px' }}>
                            <div>
                                <Link to='/get-started' > <button className='joinsignbtn'>Join</button></Link>
                            </div>
                            <div>
                                <Link to='/get-started' >  <button className='joinsign'>Sign In</button></Link>
                            </div>
                        </div>
                    </div>
                    <div className='btundis'>
                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                            <button style={{
                                backgroundColor: 'black',
                                width: '150px', border: 'none',
                                borderRadius: '50px',
                                height: '70px', fontWeight: '200',
                                color: 'white'
                            }}
                            >Get BEING PUPIL!</button>
                        </div>
                    </div>

                </div>
            </div>
        </>
    )
}

