import React from 'react';
import './contactTexts.css';

export function ContactText() {
    return (
        <>
            <div className='contacttexts' style={{color:'white'}}>
                <div className='abtTexts'>
                    <h4>Help Centre</h4>
                    <p>Need help navigating the app? Experiencing an error? 
                        Find answers to FAQs as well as our Support Team contact 
                        info here. Were ready to help!</p>
                </div>
                <div className='abtTexts'>
                <h4>Success Stories</h4>
                    <p>Nothing makes us happier at The Hive than seeing 
                        Bumble success stories! Contact us to submit your story.</p>
                </div>
                <div className='abtTexts'>
                <h4>Press Inquiries</h4>
                    <p>If you're a member of the pree, please contact us here.</p>
                </div>
                <div className='abtTexts'>
                <h4>Security Vulnerabilities</h4>
                    <p>If you're running into any security vulnerabilities with Bumble,let us know.</p>
                </div>
            </div>
        </>
    )
}