import React from 'react';
import { Header } from '../Home/Header';
import {ContactContents} from './contactcontent/contactcontent';
import {ContactText} from './contactTexts/contactTexts';
import {Aboutfooter} from '../About/Aboutfooter/aboutfooter';

export class ContactUs extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    render() {
        return (
            <>
                <div>
                    <Header />
                </div>
                <div>
                    <ContactContents />
                </div>
                <div>
                    <ContactText/>
                </div>
                <br/>
                <div>
                    <Aboutfooter />
                </div>
            </>
        )
    }
}