import React from 'react';
import { Link } from 'react-router-dom';
import './homecarousel.css';
export function Carousel() {
    return (
        < div className='homcarou' >
            <div id="demo" class="carousel slide" data-ride="carousel">


                <ul class="carousel-indicators">
                    <li data-target="#demo" data-slide-to="0" class="active"></li>
                    <li data-target="#demo" data-slide-to="1"></li>
                    <li data-target="#demo" data-slide-to="2"></li>
                </ul>

                <div class="carousel-inner">
                    <div class="carousel-item active">
                        <PicsOne />
                    </div>
                    <div class="carousel-item">
                        <PicsTwo />
                    </div>
                    <div class="carousel-item">
                        <PicsThree />
                    </div>
                </div>


                <a class="carousel-control-prev" href="#demo" data-slide="prev">
                    <span class="carousel-control-prev-icon"></span>
                </a>
                <a class="carousel-control-next" href="#demo" data-slide="next">
                    <span class="carousel-control-next-icon"></span>
                </a>
            </div>
        </div>
    )
}

function PicsOne() {
    return (
        <div>
            <div class="card img-fluid">
                <div className='caroupic'>
                    <img class="card-img-top" src="/pics/car1.jpg" style={{ width: '100%', height: '100vh' }} />
                </div>

                <div class="card-img-overlay" style={{ backgroundColor: 'black', color: 'white', width: '54%', margin: 'auto', height: '45%', paddingTop: '5%' }}>
                    <div>
                        <h1 style={{ fontWeight: 'bold' }}>
                            STUDENT OF LIFE
                       </h1>
                        <div>
                            <p>It is said that student life is golden life because student life is the most important part of human life. </p>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'space-around', width: '50%', margin: 'auto', marginTop: '30px' }}>
                            <div>
                                <Link to="try-it"><button className='joinsignbtn'>KNOW MORE</button></Link>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
