import React from 'react';
import { Header } from '../Home/Header';
import {GuideContents} from './guidecontent/guidecontent';
import {Guidetext} from './guidetext/guidetext';
import {Aboutfooter} from '../About/Aboutfooter/aboutfooter';

export class GuideLines extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    render() {
        return (
            <>
                <div>
                    <Header />
                </div>
                <div>
                    <GuideContents />
                </div>
                <div>
                    <Guidetext/>
                </div>
                <br/>
                <div>
                    <Aboutfooter />
                </div>
            </>
        )
    }
}