import React from 'react';
import './learn.css';

export function Learncontent() {
    return (
        <>
            <div>
                <img src='/pics/letter.jpg' style={{ width: '70%', margin: 'auto', height: '95vh' }} />
            </div>
            <LearnTabs />
            <div className='LearnTexts'>
                <h1 style={{ fontSize: '42px' }}>A Letter From Whitney Wolfe Herd, Bumble Founder and CEO</h1>
                <br />
                <p>
                    When I founded Bumble, it was because I saw
                    a problem I wanted to help solve. It was 2014,
                    but so many of the smart, wonderful women in my life were
                    still waiting around for men to ask them out, to take their numbers, or to
                    start up a conversation on a dating app. For all the advances women had been
                    making in workplaces and corridors of power, the gender dynamics of dating and
                    romance still seemed so outdated. I thought, what if I could flip that on its
                    head? What if women made the first move, and sent the first message?
               </p>
                <br />
                <p>
                    Nearly six years and countless Bumble weddings and babies later, were a
                    community of over 100 million across six continents. Weve celebrated 1.5
                    billion first moves. And were just getting started.
                </p>
                <br />
                <p>
                    Bumble has now grown far beyond a dating app into a networking platform,
                    allowing people of all genders to make empowered connections in all areas
                    of their lives, whether that means youre seeking a romantic relationship on
                    Bumble Date, making new friendships on Bumble BFF, or growing your professional
                    network on Bumble Bizz.
                </p>
                <p>Since 2017, the #MeToo and #TimesUp movements have put sexual harassment
                and gender discrimination at the center of the cultural conversation.
                Im more dedicated than ever to helping advance gender equality  and
                putting an end to the misogyny that still plagues society. We dont tolerate
                hate speech or bad behavior of any sort; Bumble is a platform rooted in kindness
                    and respect.</p>

                <p>In 2020, as the U.S. faces a long-overdue reckoning with centuries of anti-black
                racism, were committed to ensuring our activism is truly intersectional. Black
                    Lives Matter at Bumble, on our platform and across our business, today and always.</p>
                <p>
                    Bumble has also been making moves to better protect our community and the wider
                    online world at large from harassment. In 2019, we worked to help pass a law
                    against sending unsolicited nude images in our home state, Texas. We also added
                    a new feature that automatically blurs a nude image sent on our platform; you
                    can then decide if youd like to see it or not. Your safety on our app is
                    paramount. I want nothing more than for your connections to be both meaningful
                    and healthy.
                    </p>
                <p>Get Bumbling, and thanks for reading,</p>
                <p>WWH</p>

                <br />
                <div style={{ textAlign: 'center' }}>
                    <button style={{
                        backgroundColor: 'black',
                        width: '180px', border: 'none',
                        borderRadius: '50px',
                        height: '40px', fontWeight: '700',
                        color: 'white'
                    }}
                    >Get on BEING PUPIL!</button>
                </div>
                <br /><br />
                <div
                    style={{ width: '45%', margin: 'auto', display: 'flex', justifyContent: 'space-around', alignItems: 'center' }}>

                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-facebook-square" /></a>


                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-instagram" /></a>

                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-linkedin" /></a>

                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-twitter-square" /></a>

                </div>
            </div>
        </>
    )
}

function LearnTabs() {
    return (
        <div className='learntabs'>
            <div style={{ width: '220px', textAlign: 'left' }}>
                <img src='/pics/learntab1.jpg' style={{ width: '220px', height: '150px' }} />
                <p style={{ fontWeight: '600', fontSize: '1.1em', color: 'white' }}>How to Stay Friends With an Ex (and When You Shouldn't)</p>
            </div>
            <br />
            <div style={{ width: '220px', textAlign: 'left' }}>
                <img src='/pics/learntab2.jpg' style={{ width: '220px', height: '150px' }} />
                <p style={{ fontWeight: '600', fontSize: '1.1em', color: 'white' }}>Bumble Backs Two New Online Sexual Harassment Bills, Hires VP Public Policy in Legislative Push</p>
            </div>
            <br />
            <div style={{ width: '220px', textAlign: 'left' }}>
                <img src='/pics/learntab3.jpg' style={{ width: '220px', height: '150px' }} />
                <p style={{ fontWeight: '600', fontSize: '1.1em', color: 'white' }}>Made a Match That Didnt Quite Work Out? Heres How to Reject Someone Nicely</p>
            </div>
        </div>
    )
}