import React from 'react';
export function BffCard() {
    return (
        <>
        <div className='cardss'>
            <div>
                <CardOne/>
            </div>
            <div>
                <CardTwo/>
            </div>
            <div>
                <CardThree/>
            </div>
        </div>
        <div className='cardss'>
             <div>
                <CardFour/>
            </div>
            <div>
                <CardFive/>
            </div>
            <div>
                <CardSix/>
            </div>
        </div>
        </>
    )
}

function CardOne() {
    return (
        <>
            <div >
                <img class="card-img-top oness" src="/pics/bffcard1.jpg" />
                <div class="card-body">
                    <h5 class="card-title" style={{fontWeight:'bold', textAlign:'left', color:'white'}}>
                    UPSC CSE, SSC and Bank Exams, Railway Exams, Defence Exams.
                    </h5>
                    <p class="card-text" style={{ color:'white', fontWeight:'bold', textAlign:'left'}}>Date</p>
                </div>
            </div>
        </>
    )
}


