import React from 'react';
import { Header } from '../Home/Header';
import {TermContents} from './termsContent/termcontent';
import {Termtext} from '../Terms/termtext/termtext';
import {Aboutfooter} from '../About/Aboutfooter/aboutfooter';

export class Terms extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    render() {
        return (
            <>
                <div>
                    <Header />
                </div>
                <div>
                    <TermContents />
                </div>
                <div>
                    <Termtext/>
                </div>
                <br/>
                <div>
                    <Aboutfooter />
                </div>
            </>
        )
    }
}