import React from "react";
import { Switch, Route } from "react-router-dom";
import { Home } from '../Pages/Home/Home';
import { Getstarted } from '../Pages/getstarted/getstarted';
import { Date } from '../Pages/Date/date';
import { Bff } from '../Pages/BFF/bff';

import { CarouselOne } from '../Pages/extraPages/carouselone/carouselone';
import { Carouseltwo } from '../Pages/extraPages/carouseltwo/carouseltwo';
import {CarouselThree} from '../Pages/extraPages/CAROUSELTHREE/carouselthree';
import { Learnmore } from '../Pages/Learmore/Learnmore';
import { About } from '../Pages/About/about';
import { ContactUs } from '../Pages/contactUs/ContactUs';
import { GuideLines } from '../Pages/guidelines/guidelines';
import { Terms } from '../Pages/Terms/terms';
import { Privacy } from '../Pages/privacy/privacy';
import { Ambassadors } from '../Pages/Ambassadors/ambassadors';
import {Wisdom} from '../Pages/WISDOM/WISDOM';

export function Approot() {
    return (
        <>
            <div>
                <div class="card img-fluid" >
                    <Backgrnd />
                    <Backgrnd />
                    <Backgrnd />
                </div>
                <div class="card-img-overlay">
                    <div>
                        <Switch>
                            <Route exact path="/" component={Home} />
                            <Route path="/get-started" component={Getstarted} />
                            <Route path="/Date" component={Date} />
                            <Route path="/Bff" component={Bff} />
                            
                            <Route path="/try-it" component={CarouselOne} />
                            <Route path="/try-itTwo" component={Carouseltwo} />
                            <Route path="/try-itthree" component={CarouselThree} />
                            <Route path="/Learmore" component={Learnmore} />
                            <Route path="/about" component={About} />
                            <Route path="/contact" component={ContactUs} />
                            <Route path="/guidelines" component={GuideLines} />
                            <Route path="/terms" component={Terms} />
                            <Route path="/privacy" component={Privacy} />
                            <Route path="/Ambassadors" component={Ambassadors} />
                            <Route path="/wisdom" component={Wisdom} />
                        </Switch>
                    </div>
                </div>
            </div>

        </>

    )
}

function Backgrnd() {
    return (
        <>
            <div style={{ display: 'flex' }}>
                <div>
                    <img class="card-img-top" src='/pics/backgrnd2.png' style={{ backgroundColor: 'gray' }} />
                </div>
                <div>
                    <img class="card-img-top" src='/pics/backgrnd2.png' style={{ backgroundColor: 'gray' }} />
                </div>
                <div>
                    <img class="card-img-top" src='/pics/backgrnd2.png' style={{ backgroundColor: 'gray' }} />
                </div>
            </div>
        </>
    )
}