import React from 'react';
import './onecontent.css';

export function Onecontent() {
    return (
        <>
            <div>
                <img src='/pics/car1.jpg' style={{ width: '70%', margin: 'auto', height: '95vh' }} />
            </div>
            <CarTabs />
            <div className='ContentoneTexts'>
                <h1 style={{ fontSize: '42px' }}>STUDENT OF LIFE</h1>
                <br />
                <p>
                It is the period of pure joy and happiness, because the mind of a student is free from cares and worries of a grown-up life.
            </p>
                <br />
                <p>
                As learners, students play a crucial and active role in education.Developing excellent communication skills, being friendly and polite, and making practical applications of what they learn are some of the major roles that students play as learners in education.
            </p>
                <p>
                Being with pupil helps the student to feel that online education is best for them when they were working on their own.It gives the Students  a well-planned work week and didn't appreciate surprise assignments online any more than they appreciate them in class.
            </p>
                <br />
                
                <p>
                Our platform allows the astudents to gain a more profound understanding of the course we provide. New learning models are constantly emerging on the market as well as in our platform which give students various ways to fashion their education into something that suits them.
            </p>
                
                <br />
                <div style={{ textAlign: 'center' }}>
                    <button style={{
                        backgroundColor: 'black',
                        width: '180px', border: 'none',
                        borderRadius: '50px',
                        height: '40px', fontWeight: '700',
                        color: 'white'
                    }}
                    >Get on BEING PUPIL!</button>
                </div>
                <br /><br />
                <div
                    style={{ width: '40%', margin: 'auto', display: 'flex', justifyContent: 'space-around', alignItems: 'center' }}>

                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '45px', height: '45px', textAlign: 'center', borderRadius: '45px', backgroundColor: 'black' }}><i class="fab fa-facebook-square" /></a>


                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '45px', height: '45px', textAlign: 'center', borderRadius: '45px', backgroundColor: 'black' }}><i class="fab fa-instagram" /></a>

                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '45px', height: '45px', textAlign: 'center', borderRadius: '45px', backgroundColor: 'black' }}><i class="fab fa-linkedin" /></a>

                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '45px', height: '45px', textAlign: 'center', borderRadius: '45px', backgroundColor: 'black' }}><i class="fab fa-twitter-square" /></a>

                </div>
            </div>
        </>
    )
}

function CarTabs() {
    return (
        <div className='learntabs'>
            <div style={{ width: '220px', textAlign: 'left' }}>
                <img src='/pics/learntab1.jpg' style={{ width: '220px', height: '150px' }} />
                <p style={{ fontWeight: '600', fontSize: '1.1em', color: 'white' }}>How to Stay Friends With an Ex (and When You Shouldn't)</p>
            </div>
            <br />
            <div style={{ width: '220px', textAlign: 'left' }}>
                <img src='/pics/cartab1.jpg' style={{ width: '220px', height: '150px' }} />
                <p style={{ fontWeight: '600', fontSize: '1.1em', color: 'white' }}>Christine Developed a Mathematical Formula to Dating. It Led Her to Chris.</p>
            </div>
            <br />
            <div style={{ width: '220px', textAlign: 'left' }}>
                <img src='/pics/cartab2.jpg' style={{ width: '220px', height: '150px' }} />
                <p style={{ fontWeight: '600', fontSize: '1.1em', color: 'white' }}>Alison Was a Catholic Nun for More Than a Decade. In August, She Joined Bumble.</p>
            </div>
        </div>
    )
}