import React from 'react';
import { CarouselHead } from './Head';
import { Onecontent } from './carouselOneContent/onecontent';
import { Caronefoot } from './carouselonefoot/carouselonefoot';

export class CarouselOne extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    render() {
        return (
            <div>
                <div>
                    <CarouselHead />
                </div>
                <br /><br />
                <div>
                    <Onecontent />
                </div>
                <br />
                <Caronefoot />
            </div>
        )
    }
}