import React from 'react';
import './header.css';
import { Link } from 'react-router-dom';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';




export function Header() {
    return (
        <>
            <div className='headArrange' >
                <div >
                     <Link to='/'><h2 style={{ color: 'white', fontFamily: 'sans-serif' }}><span style={{ fontSize: '1.5em' }}>being</span> <span style={{ fontSize: '1.5em', color: '#c9e265' }}>pupil</span></h2></Link>
                    {/* <Link to='/'><img src='/pics/bumble.jpg' style={{ width: '150px', height: '50px' }} /></Link> */}
                </div>
                <div className='tabdraw'>
                    <TemporaryDrawer />
                </div>
                <div className='headOption'>
                    <div>
                        <div class="dropdown">
                            <a style={{ position: 'relative', top: '-8px', color: 'white' }}>Study</a>
                            <div class="dropdown-content">
                                <Link to='/Date'><a style={{ color: '#ffc629' }}>EDUCATOR</a></Link>
                                <Link to='/Bff'><a style={{ color: '#009abf' }}>LEARNER</a></Link>
                              <Link to='/wisdom' > <a style={{ color: '#ff6c37' }}>WISDOM</a></Link>
                            </div>
                        </div>
                    </div>
                    <div>
                        <Link to='Ambassadors'><p className='textHover' style={{ fontWeight: '600' }} >Residential</p></Link>
                    </div>
                    <div>
                        <p className='textHover' style={{ fontWeight: '600' }}>Shop</p>
                    </div>
                    <div>
                        <Link to='/about'><p className='textHover' style={{ fontWeight: '600' }}>About</p></Link>
                    </div>
                    <div >
                        <Download />
                    </div>
                </div>
            </div>
        </>
    )
}


function Download() {
    return (
        <>
            <p className='downloadBtn' data-toggle="modal" data-target="#myModal">
                Download
            </p>
            <div class="modal fade" id="myModal">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-body">
                            <button type="button" class="close" data-dismiss="modal">&times;</button>
                            <br /><br />
                            <h2 style={{ fontWeight: 'bold' }}>Be a PUPIL!</h2>
                            &nbsp;
                            <div style={{ paddingBottom: '50px' }}>
                                <form>
                                    <input type='number' placeholder='+91' style={{ width: '250px', border: '2px solid #ffc629' }} /> &nbsp;&nbsp;
                               <button type='submit' className='joinsignbtn' style={{ width: '100px' }}>JOIN</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}


const useStyles = makeStyles({
    list: {
        width: 250,
    },
    fullList: {
        width: 'auto',
    },
});

function TemporaryDrawer() {
    const classes = useStyles();
    const [state, setState] = React.useState({
        top: false,
        left: false,
        bottom: false,
        right: false,
    });

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
    };

    const list = (anchor) => (
        <div
            className={clsx(classes.list, {
                [classes.fullList]: anchor === 'top' || anchor === 'bottom',
            })}
            role="presentation"
            onClick={toggleDrawer(anchor, false)}
            onKeyDown={toggleDrawer(anchor, false)}
        >
            <Divider />

        </div>
    );

    return (
        <div>
            {['right'].map((anchor) => (
                <React.Fragment key={anchor}>
                    <Button onClick={toggleDrawer(anchor, true)} style={{ color: 'black', fontSize: '2em', border: 'none' }}><i class="fa fa-bars" aria-hidden="true" /></Button>
                    <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)} >
                        <div className='tempdraw'>
                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <div>
                                    <Link to='/'><h2 style={{ color: 'white', fontFamily: 'sans-serif' }}><span style={{ fontSize: '1em' }}>being</span> <span style={{ fontSize: '1em', color: '#ffff4d' }}>pupil</span></h2></Link>
                                </div>
                                <div>
                                    <button onClick={toggleDrawer(anchor, false)} style={{ backgroundColor: 'black', color: 'white', fontSize: '2em', border: 'none' }}>X</button>
                                </div>

                            </div>
                            <br />
                            <div>
                                <div class="dropdown">
                                    <a style={{ position: 'relative', top: '-8px', color: 'white', fontSize: '1.8em' }}>Study</a>
                                    <div class="dropdown-content" style={{ paddingLeft: '20px' }}>
                                        <Link to='/Date'><a style={{ color: '#ffc629' }}>EDUCATORS</a></Link>
                                        <br /><br />
                                        <Link to='/Bff'><a style={{ color: '#009abf' }}>LEARNER</a></Link>
                                        </div>
                                </div>
                            </div>
                            <div>
                                <Link to='Ambassadors'><p className='textHover' style={{ fontWeight: '600', color: 'white', fontSize: '1.8em' }}>Hello</p></Link>
                            </div>
                            <div>
                                <p className='textHover' style={{ fontWeight: '600', fontSize: '1.8em' }}>Shop</p>
                            </div>
                            <div>
                                <Link to='/about'><p className='textHover' style={{ fontWeight: '600', color: 'white', fontSize: '1.8em' }}>About</p></Link>
                            </div>
                            <div>
                                <div style={{ textAlign: 'center' }}>
                                    <button style={{
                                        backgroundColor: 'black',
                                        width: '180px', border: 'none',
                                        borderRadius: '50px',
                                        height: '40px', fontWeight: '700',
                                        color: 'white'
                                    }}
                                    >Get on BEING PUPIL!</button>
                                </div>
                                <br />
                                <div
                                    style={{ width: '70%', margin: 'auto', display: 'flex', justifyContent: 'space-around', alignItems: 'center' }}>

                                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-facebook-square" /></a>


                                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-instagram" /></a>

                                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-linkedin" /></a>

                                    <a href="#" style={{ fontSize: '2em', color: 'white', width: '55px', height: '55px', textAlign: 'center', borderRadius: '55px', backgroundColor: 'black' }}><i class="fab fa-twitter-square" /></a>

                                </div>
                            </div>
                        </div>
                    </Drawer>
                </React.Fragment>
            ))}
        </div>
    );
}


