import React from 'react';
import {Header} from '../Home/Header';
import {AboutContents} from './aboutcontent/aboutcontent';
import {Aboutkindone} from './aboutkindone/aboutkindone';
import {AboutkindTwo} from './aboutkindtwo/aboutkindtwo';
import {Aboutkindthree} from './aboutkindthree/aboutkindthree';
import {Aboutfooter} from './Aboutfooter/aboutfooter';

export class About extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    render() {
        return (
            <div>
                <div>
                    <Header />
                </div>
                <div>
                    <AboutContents />
                </div>
                <div style={{ marginTop: '100px' }}>
                    <Aboutkindone />
                </div>
               < div style={{ marginTop: '50%' }}>
                    <AboutkindTwo />
                </div>
               < div style={{ marginTop: '50%' }}>
                    <Aboutkindthree />
                </div>
                <div style={{backgroundColor:'black'}}>
                <div style={{ marginTop: '30%', }}>
                    <Aboutfooter />
                </div>
                </div>
                
            </div>
        )
    }
}