import React from 'react';
import './getstart.css';
export function Getstarted() {
    return (
        <>
            <div>
            <h2 style={{color:'white', fontFamily:'sans-serif'}}><span style={{fontSize:'2em'}}>being</span> <span style={{fontSize:'2em',color:'#ffff4d'}}>pupil</span></h2>
                {/* <img src='/pics/bumble.png' className='startImg' /> */}
            </div>
            <div style={{ marginTop: '100px', fontSize: '24px', fontWeight: '500' , color:'white'}}>
                <h3>Welcome! How do you want to get started?</h3>
            </div>
            <br /><br />
            <div>
                <div >
                    <button className='appleBtn'><i class="fab fa-apple" style={{ fontSize: '1.3em' }} />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    Continue with Apple</button>
                </div>
                <br />
                <div>
                    <button className='fbbtn'><i class="fab fa-facebook" />
                    &nbsp; &nbsp; &nbsp;
                    Continue with Facebook</button>
                </div>
            </div>
            <br />
            <p style={{ color: 'lightgray' }}>________________<span>or</span>_______________</p>
            <br />
            <button className='phoneBtn'>Use cell phone number</button>
            <br />
            <Startfooter />
        </>
    )
}

function Startfooter() {
    return (
        <>
        <div style={{width:'40%', margin:'auto', marginTop:'30px',paddingBottom:'30px'}}>
        <p style={{color:'white', fontSize:'12px'}}>By continuing, youre confirming that youve read and
            agree to our <a href='#' style={{color:'white'}}>Terms and Conditions</a>,
            <a href='#' style={{color:'white'}}> Privacy Policy</a> and <a href='#' style={{color:'white'}}>Cookie Policy</a></p>
        </div>    
        </>
    )
}