import React from 'react';
import './head.css';
import { Link } from 'react-router-dom';

export function CarouselHead() {
    return (
        <>
            <div style={{display:'flex', justifyContent:'space-between',alignItems:'center',marginTop:'10px'}}>
                <div style={{ display:'flex', justifyContent:'space-evenly', width:'44%'}} >
                    <div>
                        <Link to='/'> <Link to='/'><h2 style={{ color: 'white', fontFamily: 'sans-serif' }}><span style={{ fontSize: '1.5em' }}>being</span> <span style={{ fontSize: '1.5em', color: '#c9e265' }}>pupil</span></h2></Link></Link>
                    </div>
                    <div>
                        <div class="dropdown">
                            <a className='textHover'>About</a>
                            <div class="dropdown-contents">
                                <Link to='/Date'><a style={{ color: 'black' }}>EDUCATOR</a></Link>
                                <Link to='/Bff'><a style={{ color: 'black' }}>STUDENTS</a></Link>
                                <Link to='/Bizz'><a style={{ color: 'black' }}>WISDOM</a></Link>
                            </div>
                        </div>

                    </div>
                    <div>
                        <p className='textHover'>The Buzz</p>
                    </div>
                    <div>
                        <p className='textHover'>Ambassadors</p>
                    </div>
                </div>
                <div style={{display:'flex', justifyContent:'space-around', width:'30%', marginRight:'20px'}}>
                    <div>
                        <button style={{
                            backgroundColor:'black',
                             width:'210px', border:'none',
                              borderRadius:'50px',
                              height:'40px', fontWeight:'700',
                              color:'white'
                            }}
                        >Download the App</button>
                    </div>
                    <div>
                      <Link to='get-started'  ><button
                        style={{
                            backgroundColor:'black',
                             width:'130px', border:'1px solid black',
                              borderRadius:'50px',
                              height:'40px', fontWeight:'700',
                              color:'white',
                            }}
                        >Sign In</button></Link>
                    </div>
                </div>
            </div>
        </>
    )
}